<template>
  <div class="video-grid">
    <el-card
      v-for="video in videos"
      :key="video.id"
      class="video-item"
      :body-style="{ padding: '0px' }"
    >
      <div class="thumbnail-container">
        <img :src="video.thumbnail" alt="Video Thumbnail" class="video-thumbnail">
      </div>
      <div style="padding: 14px;">
        <span>{{ video.title }}</span>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      videos: [
        { id: 1, title: 'Video 1', thumbnail: 'https://via.placeholder.com/300x169' },
        { id: 2, title: 'Video 2', thumbnail: 'https://via.placeholder.com/300x169' },
        { id: 3, title: 'Video 3', thumbnail: 'https://via.placeholder.com/300x169' },
        { id: 4, title: 'Video 4', thumbnail: 'https://via.placeholder.com/300x169' },
        { id: 5, title: 'Video 5', thumbnail: 'https://via.placeholder.com/300x169' },
      ],
    };
  },
};
</script>

<style scoped>
.video-grid {
  display: grid;
  gap: 16px;
  padding: 0; /* Remove padding to prevent overflow */
  width: 100%; /* Ensure it takes the full width */
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* Set min width for grid items */
  box-sizing: border-box; /* Include padding and borders in total width */
}

.thumbnail-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
  overflow: hidden;
}

.video-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (min-width: 1024px) {
  .video-grid {
    grid-template-columns: repeat(5, 1fr); /* 5 videos per row on wider screens */
  }
}

@media (max-width: 768px) {
  .video-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 videos per row on mobile */
  }
}

/* Ensure that body and html have no overflow */
html, body {
  overflow-x: hidden; /* Prevent horizontal scrolling */
}
</style>
