<template>
  <div class="tab-bar-container">
    <!-- <el-tabs v-model="activeTab" @tab-click="handleClick" class="custom-tabs">
      <el-tab-pane label="Tekken 8" name="tekken8" class="custom-tabs"></el-tab-pane>
    </el-tabs> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeTab: this.$route.name,  // Set active tab based on current route
    };
  },
  watch: {
    $route(to) {
      // console.log("Route changed to:", to.name);
      this.activeTab = to.name;
    },
  },
  methods: {
    handleClick(tab) {
      // console.log("Tab clicked:", tab.props.name);
      if (tab.props.name !== this.$route.name) {
        this.$router.push({ name: tab.props.name });
      }
    },
  },
};
</script>

<style>
.tab-bar-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.custom-tabs .el-tabs__item.is-active {
  color: #f50a64; /* Change this to your desired active color */
}

.custom-tabs .el-tabs__item {
  color: #999; /* Change this to your desired inactive color */
}

.custom-tabs .el-tabs__item:hover {
  color: #333; /* Change hover color if needed */
}
</style>