<template>
    <div class="video-player">
      <!-- <h1>Video Player</h1> -->
      <iframe
        v-if="videoSrc"
        :width="videoWidth"
        :height="videoHeight"
        :src="videoSrc"
        title="YouTube video player"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        referrerpolicy="strict-origin-when-cross-origin"
        allowfullscreen
      ></iframe>
      <div class="downloadPanel">
        <div style="height: 15px;"></div>
        <el-button type="success" color="#f50a64" @click="openUrl">Download</el-button>
        <div style="height: 55px;"></div>

      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'VideoPlayer',
    data() {
      return {
        videoSrc: '',
        videoHeight:null,
        videoWidth:null,
        mediaFirePath:''
      };
    },
    created() {
    
    const videoId = this.gup('id')
    const si = this.gup('si')
      if (videoId && si) {
        this.videoSrc = `https://www.youtube.com/embed/${videoId}?si=${si}`;
      }

      this.videoWidth=window.screen.width*0.7;
      this.videoHeight=this.videoWidth/16*9
      this.mediaFirePath= this.gup('link')

    },
    methods: {
        gup(name, url) {
      if (!url) url = location.href;
      name = name.replace(/[[]/, "[").replace(/[\]]/, "]");
      const regexS = "[\\?&]" + name + "=([^&#]*)";
      const regex = new RegExp(regexS);
      const results = regex.exec(url);
      return results == null ? null : results[1];
    },

    openUrl() {
    
      window.open('https://www.mediafire.com/file/'+this.mediaFirePath, '_blank'); // Open in a new tab
    }
  }
  };



  </script>
  
  <style scoped>
  .video-player {
    text-align: center;
  }

  .downloadPanel{
    margin-left: 15%;
    display: flex;
    flex-direction: column;
    align-items: start;
  }
  </style>
  