import { createRouter, createWebHashHistory } from 'vue-router';
import Tekken8View from '../views/Tekken8View.vue';
import StreetFighter6View from '../views/StreetFighter6/StreetFighter6View.vue';
import DevilJinView from '../views/Tekken8/DevilJin/DevilJinView.vue';
import MacroDetailsView from '../views/MacroDetailsView.vue';
// import NotFound from '../views/NotFound.vue'; // Import the 404 component

const routes = [
  { path: '/', redirect: { name: 'tekken8' } },  // Default route redirects to Tekken 8
  { path: '/tekken8', name: 'tekken8', component: Tekken8View },
  { path: '/streetFighter6', name: 'streetFighter6', component: StreetFighter6View },
  { path: '/devil_jin', name: 'devil_jin', component: DevilJinView },
  { path: '/macroDetails', name: 'macroDetails', component: MacroDetailsView },
  // { path: '/:pathMatch(.*)*', name: 'NotFound', component: NotFound }, // Catch-all route
];


const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
